export default {
    state: {
        input: '',
        memory: ''
    },
    mutations: {
        inputEntry(state, value = '') {
            if (!parseInt(state.input) && value === '0') {
                return false
            } else {
                state.input += value
                state.memory += value
            }
        },
        clearInput(state) {
            state.input = ''
            state.memory = ''
        },
        action(state, char) {
            if (state.input) {
                state.input = ''
                switch (char) {
                    case '+':
                        state.memory += '+'
                        break
                    case '−':
                        state.memory += '-'
                        break
                    case '×':
                        state.memory += '*'
                        break
                    case '÷':
                        state.memory += '/'
                        break
                    default: break
                }
            }
        },
        result(state) {
            if (state.memory) {
                let result = eval(state.memory)
                if (!Number.isInteger(result)) {
                    result = result.toFixed(2)
                }
                state.input = result.toString()
            }
        }
    },
    actions: {
    },
    getters: {
        currentValue(state) {
            return state.input
        }
    }
}